import './App.css';
import React, { Component } from 'react';
import Router from './router/index.js';
import { Provider } from 'react-redux'; // reactreduxProvider
import store from './store'; // sotre

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router />
      </Provider>
    )
  }
}

export default App;


