import { fromJS } from 'immutable';
import { LOADING } from '../constants';

// state
const initialState = {
    loading: false
};

/**
 * reducer
 * @return
 */
const Common = (state = initialState, action) => {
    switch(action.type) {
        case LOADING: // 
            return fromJS(state).merge({loading: action.loading}).toJS();
        default:
            return state;
    }
}

export { Common};


